import {login} from '../../js/common.js'

export default{
	name:'Modal',
	props:{
		content:{
			type:String,
			default(){
				return '您确定吗'
			}
		},
		confirlText:{
			type:String,
			default(){
				return '确认'
			}
		}
	},
	data(){
		return{
			showModal:false
		}
	},
	methods:{
		getUserInfo(e){
			this.showModal = false
			console.log(e)
			var info = e.detail.userInfo
			login(info).then(res => {
				console.log(res)
				wx.showToast({
					title:'登录成功'
				})
			}).catch(err => {
				console.log(err)
			})
		},
		handleCancle(){
			this.showModal = false
		},
		init(){
			this.showModal = true
		}
	}
}
